.wp-block-kn-project-list {
	color: #10182f;

	.project-card {
		margin: 10px;
		background-color: #fff;
		border-radius: 10px;
		box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
		overflow: hidden;
		width: 300px;

		.project-card-header img {
			width: 100%;
			height: 200px;
			object-fit: cover;
		}

		.project-card-body {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: flex-start;
			padding: 20px;
			min-height: 250px;

			p {
				font-size: 13px;
				margin: 0 0 40px;
			}

			.tech-tag {
				background: #cccccc;
				border-radius: 50px;
				font-size: 12px;
				margin: 0;
				color: #fff;
				padding: 2px 10px;
				text-transform: uppercase;
				cursor: pointer;
			}
			.tag-teal {
				background-color: #47bcd4;
			}
			.tag-purple {
				background-color: #5e76bf;
			}
			.tag-pink {
				background-color: #cd5b9f;
			}
		}
	}
}
